        <b-row class="px-5 pt-3">                        
            <b-col class="bg-white p-4 col-12 mb-4 adminPila" >   
                <b-row>
                    <b-col cols="1" class="pr-0 pt-1"><div class="square" style="background-color: #9d3eff;"></div></b-col>
                    <b-col class="pl-3 pr-5">
                        <p class="stylingTitle pb-2"> Datos Manuales <img :src="icono" @click="showBtn= !showBtn" class="icon float-right pt-1" alt="Edit icon"> </p>
                        <!--  -->
                        <form @submit.prevent="editInfo(base, dias_sucios, extra_m3); wait()" >                            
                            <div class="input-group mb-3">
                                <label class="labelB0" for="inputB0">Base 0</label>
                                <input v-model="base" class="form-control" id="inputB0" type="number" :placeholder="base0">
                            </div>
                            <div class="input-group mb-3">
                                <label class="labelDS" for="inputDS">Dias sucios</label>
                                <input v-model="dias_sucios" class="form-control" id="inputDS" type="number" :placeholder="diasS">
                            </div>
                            <div class="input-group mb-3">
                                <label class="mr-5" for="inputE">$ Extra por m3</label>
                                <input v-model="extra_m3" class="form-control" id="inputE" type="number" step=".01" :placeholder="extra">
                            </div>
                            <button v-if="showBtn" 
                            :class="['btn btn-block', { 'saved' : isAFlag, 'blue' : isAFlag == false }]"
                            type="submit"><img v-if="isAFlag" :src="check" class="icon mb-1 mr-1" /> {{textButton}}
                            </button>
                        </form>
                    </b-col>                            
                </b-row>                                                     
            </b-col>
            
            <b-col class="bg-white p-4 col-6 adminPila" >
                <b-row>
                    <b-col cols="1" class="pr-0 pt-1"><div class="square" style="background-color: #00c290;"></div></b-col>
                    <b-col class="pl-3">
                        <p class="stylingTitle">Colaboradores </p>
                        <b-row>
                            <b-col>
                                <strong><p class="stylingSmall" style="color: #838383;">Nombre: </p></strong>
                                <ul>
                                    <li v-for="item in equipo" :key="item.id" class="stylingSmall mb-1">{{item.nombre}}</li>
                                </ul>
                            </b-col>
                            <b-col>
                                <strong><p class="stylingSmall" style="color: #838383;">Bono al día: </p></strong>
                                <ul style="list-style-type: none;">
                                    <li class="stylingSmall mb-1" style="color: #fd832d;">$ {{bono | rand }}</li>
                                    <li class="stylingSmall mb-1" style="color: #fd832d;">$ 970.00</li>
                                    <li class="stylingSmall mb-1" style="color: #fd832d;">$ 970.00</li>
                                    <li class="stylingSmall mb-1" style="color: #fd832d;">$ 970.00</li>
                                    <li class="stylingSmall mb-1" style="color: #fd832d;">$ 970.00</li>
                                    <li class="stylingSmall mb-1" style="color: #fd832d;">$ 970.00</li>
                                    <li class="stylingSmall mb-1" style="color: #fd832d;">$ 970.00</li>
                                </ul>
                            </b-col>
                        </b-row>
                        
                    </b-col>
                </b-row>                       
            </b-col>
            
        </b-row>      
    </div>
</template>


<script>
import icono from '../assets/icono_edit.png';
import check from '../assets/icono_check.png';

export default {
    name: 'FormInfoBase',
    props: {
        equipo:{
            required: true
        },
        extra:{
            required: true
        },
        diasS:{
            required: true
        },
        base0:{
            required: true
        },
        bono:{
            required: true
        }
    },
    data() {
        return {
            base: '',
            dias_sucios: '',
            extra_m3: '',
            icono: icono,
            showBtn: false,
            check: check
        }
    },
    computed: {
        isAFlag() {
            return this.$store.getters["corte/flag"];;
        },
        textButton() {
            return this.$store.getters["corte/getNotif"];
        },
    },
    methods: {
        editInfo: function (base, dias_sucios, extra_m3) {
            this.$store.dispatch("corte/editInfo", {
                base: base,
                dias_sucios: dias_sucios,
                extra_m3: extra_m3
            });
        },
        wait: function(){
            setTimeout(() => this.$router.go(), 1000);
        }
    },
    filters:{
        rand: (value) => {
            if (!value) return '';
            if (typeof value === 'number') {
                return value.toFixed(2);
            }
            return value;
        }
    }
}
</script>
